import './styles.css';
import bannerImg from './cat.png';
import { makeAboutPage } from './pages/about';
import { makeMenuPage } from './pages/menu';

const navLinks = document.querySelectorAll("header > nav > button");
const header = document.querySelector("header");
const contentSection = document.querySelector("#content")


window.onload = makeHomePage;
let menuPage = makeMenuPage(); 
let selectedItem = document.querySelector("nav > #home");
selectedItem.classList.add("highlight");

header.addEventListener("click", (event) => {
     
    if(event.target.id !== "header" && event.target.id !== "nav"){
        selectedItem.classList.remove("highlight");
        selectedItem = event.target;
        selectedItem.classList.add("highlight");
    }
    
    if(event.target.id == "menu"){
        contentSection.replaceChildren(menuPage);
        
        
    }
    if(event.target.id == "home"){
        contentSection.replaceChildren();
        makeHomePage();
    }

    if(event.target.id == "about"){
        contentSection.replaceChildren(makeAboutPage());
        
    }
});











function makeHomePage(){
    const banner = document.createElement("div");
    const txtP1 = document.createElement("div");
    const txtP2 = document.createElement("div");
    const txtP3 = document.createElement("div");
    const p1 = document.createElement("p");

    banner.classList.add("img");
    banner.style.cssText = `background: url(${bannerImg})`

    p1.innerHTML = `
        Welcome to <strong style='color: #00b4d8'><i>Bistro Lumière</i></strong>, where culinary artistry meets exceptional service. Our restaurant offers a charming and cozy atmosphere, perfect for sharing delightful meals with your loved ones. Whether you're celebrating a special occasion or simply savoring a leisurely dinner, we're here to make your dining experience memorable.
    `;

    txtP2.innerText = `
        At Bistro Lumière, our menu features a variety of mouthwatering dishes crafted with fresh, locally sourced ingredients. From classic French bistro fare to contemporary culinary creations, we offer something to satisfy every palate. Don't miss our chef's daily specials, which showcase the finest seasonal flavors.
    `;

    txtP3.innerHTML = `
        Ready to experience Bistro Lumière for yourself? Browse our menu to explore our culinary offerings, and don't forget to make a reservation to secure your spot. We can't wait to welcome you to our bistro!
    `;

    p1.classList.add("p1");
    txtP1.appendChild(p1)
    txtP2.classList.add("txt");
    txtP3.classList.add("txt");

    contentSection.append(banner, txtP1, txtP2, txtP3)

}




