'use strict';
import Img1 from '../img1.jpg';
import Img2 from '../img2.jpg';
import Img3 from '../img3.jpg';
import Img4 from '../img4.jpg';
import Img5 from '../img5.jpg';
import Img6 from '../img6.jpg';

const imgs = [Img1, Img2, Img3, Img4, Img5, Img6];
let menuItems = [];
let menuInfo = [];


export function makeMenuPage(){

    const menuesContainer = document.createElement("div");
    menuesContainer.classList.add("menues")


    for(let i = 0; i <6; i++){
        const div = document.createElement("div");
        const overlay = document.createElement("div");
        div.style.cssText = `background: url(${imgs[i]}); background-size: cover; `;
        overlay.classList.add("overlay")
        div.appendChild(overlay)
        menuItems.push(div);
        menuInfo.push(overlay);
        menuesContainer.appendChild(div)
    }

    menuInfo.forEach(item => {
        const h5 = document.createElement("h2");
        const p = document.createElement("h3");
        h5.style.cssText = 'margin: 0;';
        p.style.cssText = 'margin: 0; font-size: 16px; color: rgb(189, 183, 183);';
        item.append(h5, p);
    });

    menuInfo[0].firstChild.innerText = "Chocolate Mousse";
    menuInfo[0].lastChild.innerHTML = "-  Rich and smooth chocolate dessert. <strong style='color: #8ecae6'>$8</strong>";

    menuInfo[1].firstChild.innerText = "Signature Cocktails";
    menuInfo[1].lastChild.innerHTML = "- Custom cocktails crafted by our mixologists.  <strong style='color: #8ecae6'>Prices vary.</strong>";

    menuInfo[2].firstChild.innerText = "Tarte Tatin";
    menuInfo[2].lastChild.innerHTML = "- Caramelized apple tart with a buttery crust.  <strong style='color: #8ecae6'>$9</strong>";

    menuInfo[3].firstChild.innerText = "Crème Brûlée";
    menuInfo[3].lastChild.innerHTML = "- Creamy custard with a caramelized sugar topping.  <strong style='color: #8ecae6'>$10</strong>";

    menuInfo[4].firstChild.innerText = "Grilled Salmon";
    menuInfo[4].lastChild.innerHTML = "- Served with lemon butter sauce and seasonal vegetables.  <strong style='color: #8ecae6'>$25</strong>";

    menuInfo[5].firstChild.innerText = "Coq au Vin";
    menuInfo[5].lastChild.innerHTML = "-  Chicken braised in red wine with mushrooms and onions.  <strong style='color: #8ecae6'>$22</strong>";
    
    menuItems.forEach((item) => {
       
        item.addEventListener('mouseover', () => {
            item.firstChild.classList.add("show")
            item.firstChild.firstChild.classList.add("showTxt")
            item.firstChild.lastChild.classList.add("showTxt")
            console.log("before: ", item.firstChild.classList)
        });

        item.addEventListener('mouseout', () => {
            item.firstChild.classList.remove("show")
            item.firstChild.firstChild.classList.remove("showTxt")
            item.firstChild.lastChild.classList.remove("showTxt")
            console.log("after: ", item.firstChild.classList)
        });
    
    });

   
    
    
    return menuesContainer;
}