___CSS_LOADER_EXPORT___.push([module.id, `* {
    box-sizing: border-box;
    font-family: "roboto", sans-serif;
}

body {
    margin: 0;
    position: relative;
}

nav {
    
    display: flex;
    justify-content: center;
    gap: 40px;
    
}

header {
    background-color: #1f1e1e ;
    padding: 14px ;
    width: 100vw;
    height: 70px;
     
}



nav > button {
    width: 120px;
    height: 40px;
    font-size: 18px;
    color: rgb(8, 8, 8);
    cursor: pointer;
    background-color: #9c9797;
    border: 0.5px solid #9c979700;
    outline: none;
    transition: background-color 0.3s ease;
}

/* nav > button:hover {
    background-color: #b9b9b9;;
} */

#content {
    width: 100vw;
    height: calc(100vh - 70px);
    padding: 40px 200px;
    background-color: #2e2d2d;
    color: #FFFFFF;
    font-size: 19px;
    display: grid;
    grid-template-columns: 250px 1fr;
    grid-template-rows: 1fr 1fr 1fr;
}

#content > div {
    
    
}

.img {
    grid-column: 1 / 2;
    grid-row: 1 / 4;
    background: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-size: cover;
    border-radius: 5px;

}

.txt {
    padding: 0px 25px;
    font-size: 20px;
    line-height: 1.5;
    letter-spacing: 1.2px;
}

.p1 {
    padding: 0px 25px;
    font-size: 20px;
    margin-top: 0px;
   
    line-height: 1.5;
    letter-spacing: 1.2px;
}

.menues {
    width: 100%;
    grid-column: 1 / -1;
    height: 80vh;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    gap: 20px;
    
    
}

.menues > * {
    width: 350px;
    border-radius: 5px;
    cursor: pointer;
}

.overlay {
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.562);
    visibility: hidden;
    opacity: 0;
    display: flex;
    flex-direction: column;
    padding: 25px 20px;
    justify-content: flex-end;
    gap: 10px;
    
    color: white;
    font-size: 15px;
    transition: all 0.3s ease;
    
}
.overlay > * {
    transform: translatex(-80px);
    opacity: 0;
    transition: all 0.3s ease;
}

.showTxt {
    opacity: 1;
    transform: translateX(0);
}
.overlay.show {
    opacity: 1;
    visibility: visible;
}

.contact-info {
    
    width: 100%;
    grid-column: 1/ -1;
    grid-row: 1 / 3;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding: 10px 15px;
   
}


.highlight {
    background-color: #b23a48;
}

