'use strict';

export function makeAboutPage(){
    const contactInfoContainer = document.createElement("div");
    const locationDiv = document.createElement("div");
    const phoneDiv = document.createElement("div");
    const hoursOfWorkDiv = document.createElement("div");


    const location = document.createElement("h2");
    const address = document.createElement("p")
    
    location.style.cssText = "color: #d00000; font-size: 25px";
    address.style.cssText = "color: #a2d2ff; font-size: 19px"
    
    location.innerText = `Location:`;
    address.innerText = `- 456 Ocean Drive, Seaside City, CA 90210`
    
    const phone = document.createElement("h2");
    const phoneNumber = document.createElement("p")

    phone.innerText = `Phone:`;
    phoneNumber.innerText = `- (555) 123-4567`
    phone.style.cssText = "color: #d00000; font-size: 25px";
    phoneNumber.style.cssText = "color: #a2d2ff; font-size: 19px"


    
    const HoursOfWork = document.createElement("h2");
    const HoursOfWorkData = document.createElement("p")

    HoursOfWork.innerText = `Hours of Work:`;
    HoursOfWorkData.innerText = `- Monday - Friday: 11:00 AM - 10:00 PM\n\n- Saturday - Sunday: 9:00 AM - 11:00 PM`;

    HoursOfWork.style.cssText = "color: #d00000; font-size: 25px";
    HoursOfWorkData.style.cssText = "color: #a2d2ff; font-size: 19px"

    locationDiv.append(location, address);
    phoneDiv.append(phone, phoneNumber);
    hoursOfWorkDiv.append(HoursOfWork, HoursOfWorkData)

    contactInfoContainer.append(locationDiv, phoneDiv, hoursOfWorkDiv)
    contactInfoContainer.classList.add("contact-info")
    console.log("About got called");

    
    return contactInfoContainer;
    
}